.iconContainer {
  display: inline-block;
  margin-left: 10px;
}

.icon {
  display: flex;
  height: 25px;
  width: auto;
  transition: 200ms all;
  align-items: center;
  justify-content: center;
  max-width: 50%;
}

.icon:hover{
  filter: saturate(200%);
  transform: scale(1.1);
  cursor: pointer;
}

.icon img {
  flex: 1;
  height: 100%;
  width: auto;
  display: inline-block;
}

.icon .typeDescription {
  flex: 4;
  padding-left: 5px;
  line-height: 25px;
  display: inline-block !important;
}

